<template>
	<el-container v-loading="loading">
		<div class="rise x add">
			<div class="title">{{title}}</div>
			<div class="content">
				<div class="item" v-for="(item, index) in data" :key="index">
					<span>{{item.name}}</span>
					<el-input v-if="item.module === 'name'" v-model="name" :placeholder="item.placeholder"/>
					<el-input v-if="item.module === 'count'" v-model="count" :placeholder="item.placeholder"/>
					<el-input v-if="item.module === 'price'" v-model="price" :placeholder="item.placeholder"/>
					<el-date-picker v-if="item.module === 'beginTime'" v-model="beginTime" :placeholder="item.placeholder" type="date"/>
					<el-date-picker v-if="item.module === 'endTime'" v-model="endTime" :placeholder="item.placeholder" type="date"/>
				</div>
				<picupload label="商品图片：" ref="picup" :defaultValue="picDefault"/>
				<!-- 商品简介 -->
				<div class="item">
					<span>商品简介：</span>
					<el-input type="textarea" :rows="4" placeholder="请输入商品简介" v-model="remark" />
				</div>
			</div>
			<div class="btn-list">
				<el-button @click="cancle()" class="btn">取消</el-button>
				<el-button @click="save()" class="btn">保存</el-button>
			</div>
		</div>
	</el-container>
</template>

<script>
import queryString from "querystring";
import { DateDay } from "utils/formData";
import Picupload from "components/picupload";
const titles = ["新建", "编辑"];
const defaultV = [
  { name: "商品名称：", placeholder: "请输入商品名称", module: "name" },
  { name: "商品数量：", placeholder: "请输入商品数量", module: "count" },
  { name: "商品价格：", placeholder: "请输入商品价格", module: "price" },
  {
    name: "开始日期：",
    placeholder: "请选择开始日期",
    module: "beginTime",
    type: "date"
  },
  {
    name: "结束日期：",
    placeholder: "请选择结束日期",
    module: "endTime",
    type: "date"
  }
];

export default {
  name: "add",
  data() {
    return {
      title: "",
      id: "",
      name: "",
      count: "",
      price: "",
      beginTime: "",
      endTime: "",
      remark: "",
      type: "",
      picDefault: null, //默认地址
      data: defaultV,
      loading: false
    };
  },
  components: {
    Picupload
  },
  mounted() {
    this.dataInit();
  },
  methods: {
    dataInit() {
      const { type, id } = this.$route.query;
      this.type = type;
      this.id = id;
      if (type !== "edit") return (this.title = titles[0]);
      this.title = titles[1];
      this.detailAjax();
    },
    editInit(res) {
      const { name, count, price, beginTime, endTime, pics } = res.data;
      this.name = name;
      this.count = count;
      this.price = price;
      this.beginTime = beginTime;
      this.endTime = endTime;
      this.picDefault = pics;
    },
    detailAjax() {
      //查看详情
      this.loading = true;
      this.api.get(`/goods/${this.id}`).then(res => {
          this.loading = false;
          this.editInit(res);
        })
        .catch(err => {
          console.log(err);
          this.loading = false;
          this.$message(err.message);
        });
    },
    editAjax() {
      const params = {
        name: this.name,
        beginTime: DateDay(this.beginTime),
        endTime: DateDay(this.endTime),
        remark: this.remark,
        price: this.price,
        count: this.count
      };
      params.pics = this.$refs.picup.getData();
      const axiosType = ["post", "put"];
      let type = axiosType[0];
      if (this.type === "edit") {
        params.id = this.id;
        type = axiosType[1];
      }
      this.loading = true;
      this.api[type](`/goods`, params, "form").then(res => {
          this.loading = false;
          this.$router.go(-1);
        })
        .catch(err => {
          this.loading = false;

          console.log(err);
          this.$message(err.message);
        });
    },
    save() {
      this.editAjax();
    },
    cancle() {
      //取消
      this.$router.go(-1);
    }
  }
};
</script>
 
