<template>
    <div class="pic upload">
        <div class="picTop">
            <span>{{label}}</span>
            <el-container v-loading="loading">
                <div tabindex="0" class="el-upload el-upload--text">
                    <label for="upPhoto" class="el-icon-plus avatar-uploader-icon"/>
                    <input @change="fileChange" type="file" name="file" class="el-upload__input" id="upPhoto">
                </div>
                <div v-if="process" class="process">
                    上传进度：{{process}}
                </div>
            </el-container>
        </div>
        
        <ul class="list" v-show="imageList.length > 0">
            <li v-for="(item, index) in imageList" :key="index">
                <div>链接地址：<a :href="item" target="_blank">{{item}}</a></div>
                <div @click="del(item, index)">删除</div>
            </li>
        </ul>
    </div>
</template>

<script>
import { FileChange } from "utils/formData";
export default {
    props:['label','defaultValue'],
    data() {
        return {
            imageList: [],
            loading: false,
            process: ''
        };
    },
    methods: {
        fileChange(e) {
            this.loading = true;
            this.process = '';
            FileChange(e, res => {
                this.loading = false;
                this.process = '';
                this.imageList.unshift(res.url);
            }, progressEvent => {
                this.process = progressEvent;
            });
        },
        del(obj, index){
            const list = this.imageList.filter(item => {
                return obj !== item;
            })
            this.imageList = list;
        },
        getData(){
            let str = this.imageList.join(',');
            return str;
        }
    },
    watch: {
        defaultValue(){
            this.imageList = this.defaultValue.split(',')
        }
    }
};
</script>

<style lang="less" scope>
@import url("./../assets/less/color.less");
@pic-border:#dcdfe6;
.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 150px;
    height: 150px;
    line-height: 150px;
    border: 1px solid @pic-border;
    border-radius: 6px;
    text-align: center;
}
.pic.upload{
    .picTop{
        display: flex;
        align-items: center;
        >span{
            display: inline-block;
            width: 120px;
            text-align: right;
        }
        .process{
            height: 150px;
            line-height: 150px;
            margin-left: 20px;
        }
    }    
    .avatar-uploader-icon{
        cursor: pointer;
    }
    .el-loading-mask{
        width: 148px;
        height: 148px;
        left: 2px;
        top: 2px;
    }
    .list{
        margin: 0;
        padding: 0;
        li{
            list-style: none;
            display: flex;
            margin-left: 120px;
            div{
                margin: 10px 20px 10px 0;
                &:last-child{
                    cursor: pointer;
                }
            }
        }
    }
}
</style>